  .countdown div {
    padding: 10px;
    margin: 0;
  }
}

.noUi-handle {
  background-color: #fff;
  border: 8px solid #2575dc;
  box-shadow: none;
  border-radius: 50%;
}

.noUi-target {
  background-color: #e0e0e0;
  border: 0;
  box-shadow: none;
  border-radius: 30px;
}

.noUi-connect {
  background-color: #2575dc;
}

.noUi-horizontal {
  height: 10px;
  margin-bottom: 30px;
}

.noUi-horizontal .noUi-handle {
  top: -8px;
  width: 26px;
  height: 26px;
  cursor: pointer;
  outline: none;
}

.noUi-horizontal .noUi-handle::before, .noUi-horizontal .noUi-handle::after {
  display: none;
}

.lazyload,
.lazyloading {
  opacity: 0;
}

.lazyloaded {
  opacity: 1;
  transition: opacity 300ms;
}

@media (prefers-reduced-motion: reduce) {
  .lazyloaded {
    transition: none;
  }
}
